import React, { useCallback, useEffect, useRef, useState } from "react";

function useMouseScroll({ selector, direction = "vertical", factor = 1 }) {
  const stateRef = useRef({
    isDragging: false,
    startX: 0,
    startY: 0,
    scrollLeft: 0,
    scrollTop: 0,
  });

  useEffect(() => {
    const element = typeof selector === "string" ? document.querySelector(selector) : selector.current || selector;

    if (!element) return;

    const handleMouseDown = (e) => {
      stateRef.current = {
        isDragging: true,
        startX: e.clientX,
        startY: e.clientY,
        scrollLeft: element.scrollLeft,
        scrollTop: element.scrollTop,
      };
      element.style.cursor = "grabbing";
      element.style.userSelect = "none";
    };

    const handleMouseMove = (e) => {
      if (!stateRef.current.isDragging) return;

      const { startX, startY, scrollLeft, scrollTop } = stateRef.current;
      const dx = e.clientX - startX;
      const dy = e.clientY - startY;

      if (direction !== "vertical") element.scrollLeft = scrollLeft - dx;
      if (direction !== "horizontal") element.scrollTop = scrollTop - dy;
    };

    const handleMouseUp = () => {
      stateRef.current.isDragging = false;
      element.style.cursor = "grab";
      element.style.userSelect = "";
    };

    element.addEventListener("mousedown", handleMouseDown);
    document.addEventListener("mousemove", handleMouseMove);
    document.addEventListener("mouseup", handleMouseUp);

    return () => {
      element.removeEventListener("mousedown", handleMouseDown);
      document.removeEventListener("mousemove", handleMouseMove);
      document.removeEventListener("mouseup", handleMouseUp);
    };
  }, [selector, direction]);
}

/**
 *  Hook click
 * @returns {function} fn(event, delay, callback) -  onTouchStart
 */
function useLongPress() {
  let timer = null;
  let isLongPress = false;

  const startLongPress = (event, delay, callback) => {
    isLongPress = false; // 

    // 
    timer = setTimeout(() => {
      isLongPress = true;
      event.preventDefault(); // 
      callback(event);
    }, delay);

    // 
    const touchTarget = event.currentTarget || event.target;
    const cancelLongPress = (e) => {
      clearTimeout(timer);
      //  click 
      if (!isLongPress) return;
      //  touchmove/touchend 
      e.preventDefault();
    };

    touchTarget.addEventListener("touchmove", cancelLongPress, { once: true, passive: false });
    touchTarget.addEventListener("touchend", cancelLongPress, { once: true, passive: false });
  };

  return startLongPress;
}

/**
 *  Hook
 * @returns {function} fn(event, delay, callback) -  onMouseEnter
 */
function useHoverDelay() {
  const timerRef = useRef(null);
  const isHoveringRef = useRef(false);

  /**
   * 
   * @param {Event} event - 
   * @param {number} delay - ()
   * @param {Function} callback - 
   * @returns {Function} ()
   */
  const startHoverDelay = useCallback((event, delay, callback) => {
    // 
    if (timerRef.current) {
      clearTimeout(timerRef.current);
      timerRef.current = null;
    }

    isHoveringRef.current = true;

    // 
    timerRef.current = setTimeout(() => {
      if (isHoveringRef.current) {
        callback(event);
      }
    }, delay);
  }, []);

  const cleanup = () => {
    if (timerRef.current) {
      clearTimeout(timerRef.current);
      timerRef.current = null;
    }
    isHoveringRef.current = false;
  };

  return { startHoverDelay, cleanup };
}


export {
  useMouseScroll,
  useLongPress,
  useHoverDelay
}